<template>
    <div class="Albumlist">
        <div class="container-fluid">
            <ul class="media-list row">
                <li class="media row col-sm-12 col-lg-6" v-for="(album, index) in albums" :key="album.id">
                    <div class="col-sm-12 col-md-4">
                        <img class="media-left media-object mw-100" v-bind:src="album.coverBig">
                    </div>
                    <div class="media-body col-md-12">
                        <h2 class="h3">{{ album.name }}</h2>
                        <h3 class="h4">{{ album.artist.name }}</h3>
                        <songlist v-bind:album_id="album_ids[index]"></songlist>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import songlist from '@/components/Songlist'

export default {
    name: 'Albumlist',
    props: ['source'],
    components: {
        'songlist': songlist,
    },
    data () {
        return {
            albums: [],
            songs: [],
            album_ids: [],
        }
    },
    methods: {
        getAlbumList: function (source) {
            axios.get('https://douting.leanapp.cn/api/search/album/qq?&limit=3&key=' + source)
                .then(response => {
                    this.albums = response.data.albumList;

                    let ids = this.albums.map(album => {
                        return album.id
                    });

                    this.album_ids = ids;
                });
        }
    },
    created: function () {
        this.getAlbumList(this.source);
    },
    watch: {
        source: function (val) {
            this.getAlbumList(val);
        }
    }
}
</script>

<style scoped>
    h2, h3 {
        padding-left: 15px;
    }
    
    .media-object {
        max-width: 100%;
        padding: 10px;
        border-radius: 15px;
    }

    .media {
        border-top: 1px solid lightgrey;
        padding-top: 20px;
    }

</style>




